.calendarContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Calendar page header */
:global(.fc .fc-header-toolbar) {
  color: #000000;
}

/*  Calendar size & Background  */
:global(.fc .fc-view-harness) {
  background-color: #383838;
  width: 80vw;
}

/* Days font color */
:global(.fc .fc-daygrid-day-number) {
  color: #00ff1a;
}

/* Current day background */
:global(.fc .fc-daygrid-day.fc-day-today) {
  background-color: #023900b7;
}

/*   Buttons   */
:global(.fc .fc-button-primary) {
  background-color: rgba(255, 0, 0, 0.742); 
  border-color: rgb(164, 164, 164); 
  color: #ffffff; 
}

:global(.fc .fc-button-primary:disabled) {
  background-color: rgb(174, 0, 0);
  border-color: rgb(164, 164, 164);
  color: white;
}

:global(.fc .fc-button-primary:hover) {
  background-color: rgb(86, 0, 0);
  border-color: white;
  color: white;
}

/* Modal */

.calendarModal {
  background-color: transparent;
}

.calendarModalHeader {
  background-color: #005205;
  color: #ffffff;
  font-weight: bold;
  font-family: 'Agbalumo';
  border-bottom: none;
}

.calendarModalBody {
  background-color: #002809;
}

.calendarModalFooter {
  background-color: #000000;
  color: #ffffff;
  border-top: none;
}

/* Paragraphs in modal body */
.calendarModalBody p {
  color: #d3d3d3; 
  margin-bottom: 2px;
  font-size: 90%;
}

/* Strong text in modal body */
.calendarModalBody strong {
  color: #c49600; 
  font-weight: bold;
  font-family: 'Noto Serif', serif;
  font-size: 100%;
  margin-bottom: 2px;
}

/* Close button */
.closeButton {
  background-color: #686868; 
  color: white;
  border: none;
}

.closeButton:hover {
  background-color: #7d7d7d; 
  color: rgb(24, 210, 0);
  border: none;
}

/* Edit Task button */
.editButton {
  background-color: #740000;
  color: white;
  border: none;
}

.editButton:hover {
  background-color: #8a0000;
  color: rgb(24, 210, 0);
  border: none;
}

